div.container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
   
    
}
.reviewsHeading{
    font: 400 1.8vmax "Roboto";
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.615);
    width: 40%;
    color: rgba(0, 0, 0, 0.615);
    margin-bottom: 2vmax;
}

.reviews{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 50%;
    
}

.reviews>div>div>div>div>div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.reviews>div>div>div>div>div>div{
    display: grid;
    place-items: center;
}



@media screen and (max-width:600px) {
    .reviews{
        min-width: 90%;
    }
}