.reviewCard {
  width: 100%;
  min-height: 50vh;
  transition: all 0.5s;
  border-radius: 20px;
  padding: 2vmax 4vmax;
  margin: 0 2vmax;
  border: 1px solid rgba(0, 0, 0, 0.35);
}

.reviewCard:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 5px 5px 5px;
}

.reviewCard img {
  width: 10vmax;
}

.reviewCard > span > span {
  font-size: 20px;
}
