.productDetail {
    /* background-color: rgb(214, 214, 214); */
    width: 100vw;
    max-width: 100%;
    padding: 6vmax;
    box-sizing: border-box;
    display: flex;

}

.productDetail>div {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 2vmax;
    box-sizing: border-box;
    border: 1px solid #fff;
}

.productDetail>.carouesel>div {
    width: 100%;
}

.productDetail>div:last-child {
    align-items: flex-start;
}

.productDetail>.carouesel>div>div>div>div>div>div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.CaroueselImage {
    /* width: 30vmax; */
    height: 25vmax;
}

.detailsBlock-1 {
    margin-bottom: 2vmax;
}

.detailsBlock-1>h2 {
    color: rgb(54, 54, 54);
    font: 600 1.6vmax "Roboto";
}

.detailsBlock-1>p {
    color: rgba(54, 54, 54, 0.582);
    font: 200 1vmax cursive;
}

.detailsBlock-2 {
    display: flex;
    justify-content: flex-start;
    align-items: center;
   
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 80%;
    padding: 1vmax 0;
}

.detailsBlock-2 > span:first-child {
    font-size: 20px;
}
.detailsBlock-2-span {
    color: rgba(54, 54, 54, 0.582);
    font: 200 1vmax cursive;
    padding-left: 1vmax;
}

.detailsBlock-3 {
    width: 80%;
}

.detailsBlock-3>h1 {
    color: rgba(17, 17, 17, 0.795);
    font: 400 1.8vmax "Roboto";
    margin: 1vmax 0;
}

.detailsBlock-3-1 {
    display: flex;
    align-items: center;

}

.detailsBlock-3>p {
   
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    padding: 1vmax 0;
    color: rgba(0, 0, 0, 0.651);
    font: 400 1.2vmax "Roboto";
    margin: 1vmax 0;
}

.detailsBlock-4 {
    color: rgba(0, 0, 0, 0.897);
    font: 500 1.2vmax sans-serif;
    display: inline;
    width: 80%;
    border-top: 1px solid rgba(0, 0, 0, 0.205);
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
padding: 0.6vmax 0;
}

.detailsBlock-4>p {
    color: rgba(0, 0, 0, 0.534);
    font: 300 0.8vmax sans-serif;
    display: inline;
   

}
.submitReviewBtn {
    margin-left: 0 !important;
    
}

.submitReviewBtn:hover {
    transform: scale(1.1);
}
.submitDailog{
    display: flex;
    flex-direction: column;
}
.submitDailogTextArea{
    border: 1px solid rgba(0, 0, 0, 0.651);
    margin:1vmax 0;
    outline: none;
    padding: 1rem;
    font: 300 1rem "Roboto";
 
}

@media screen and (max-width:600px) {
    .productDetail {
        flex-direction: column;
        height: unset;
        padding: 3vmax;
    }

    .productDetail>div:last-child {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .detailsBlock-1>h2 {
        font: 600 4vmax "Roboto";
        text-align: center;
    }

    .detailsBlock-1>p {
        font: 200 1.5vmax cursive;
    }

    .detailsBlock-2 {
        width: 90%;
        justify-content: center;
        border-top: none;
    }

    .detailsBlock-2>span {
        font: 200 1.5vmax cursive;
    }

    .detailsBlock-3 {
        width: 90%;
    }

    .detailsBlock-3>h1 {
        font: 800 4vmax "Roboto";
        text-align: center;
    }

    .detailsBlock-3-1 {

        justify-content: center;
    }

    .detailsBlock-3-1-1>button {
        padding: 1.5vmax;
        font-size: 2vmax;
    }

    .detailsBlock-3-1-1>input {
        padding: 1.5vmax;
        font-size: 2vmax;
    }

    .detailsBlock-3>p {

        padding: 2vmax 0;
        font: 400 2.5vmax "Roboto";
        border-top: none;
        text-align: center;
    }

    .detailsBlock-4 {
        width: 90%;
        text-align: center;
        padding: 2vmax 0;
        font: 500 2vmax sans-serif;
        border-top: 1px solid rgba(0, 0, 0, 0.205);
        border-bottom: 1px solid rgba(0, 0, 0, 0.205);
        display: inline;
    }

    .detailsBlock-4>p {
        display: inline;

        font: 300 1.5vmax sans-serif;
    }
}